<template>
  <b-overlay :show="!propertyTitle" rounded="sm" :opacity="1">
    <div class="preview-form">
      <div class="form-container">
        <h2 class="form-heading">
          {{ propertyTitle }}
        </h2>

        <!-- Property Details -->
        <div class="address section">
          <div class="container">
            <div class="svg-icon">
              <svg-icon
                fill="#262D3D"
                name="dollar"
                height="24"
                width="24"></svg-icon>
            </div>
            <div class="text">
              <p class="font-size-semi-large font-weight-bold mb-0">
                {{ $t('property.propertyPrice') }}
              </p>
              <p v-if="form4 && form4.daily_price" class="my-2">
                <span class="font-weight-bold font-size-semi-large">
                  {{ form4.daily_price }}
                </span>
                /{{ $t('form.shortTerm') }}
              </p>
              <p v-if="form4 && form4.monthly_price">
                <span class="font-weight-bold font-size-semi-large">
                  {{ form4.monthly_price }}
                </span>
                /{{ $t('form.longTerm') }}
              </p>
            </div>
          </div>
        </div>
        <div class="address section">
          <div class="container">
            <div class="svg-icon">
              <svg-icon
                fill="#262D3D"
                name="MapPin"
                height="24"
                width="24"></svg-icon>
            </div>
            <div class="text">
              <p class="font-size-semi-large font-weight-bold mb-0">
                {{ $t('form.PropertyAddress') }}
              </p>
              <p>
                <span class="font-weight-bold">
                  {{ form1 ? form1.city.location_name : '' }}
                </span>

                {{
                  form1 && form1.district ? form1.district.location_name : ''
                }}
                {{
                  form1 && form1.neighborhood
                    ? form1.neighborhood.location_name
                    : ''
                }}
              </p>
            </div>
          </div>
        </div>
        <!-- Property Description -->
        <div class="section">
          <div class="container">
            <div class="text">
              <p class="font-size-semi-large font-weight-bold mb-2">
                {{ $t('form.propertyDescription') }}
              </p>
              <span class="light-gray font-weight-bold">
                {{ form4 ? form4.propertyDescription : '' }}
              </span>
            </div>
          </div>
        </div>
        <!-- Property Images -->
        <div class="section" v-if="form5">
          <div class="container">
            <div class="text">
              <p class="font-size-semi-large font-weight-bold mb-2">
                {{ $t('form.propertyPhotos') }}
              </p>
              <div class="images mobile-scroll">
                <div class="image" v-for="(image, index) in form5" :key="index">
                  <img :src="image" width="120" height="100" />
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- Furnish Type -->
        <div class="section" v-if="form2">
          <div class="container">
            <p class="font-size-semi-large font-weight-bold">
              {{ $t(`property.furnishing_status`) }}
            </p>
            <span
              class="chip"
              v-for="(furnishType, index) in form2.furnishTypes"
              :key="index">
              {{ furnishType.text }}
            </span>
          </div>
        </div>
        <!-- Property Type -->
        <div class="section" v-if="form2">
          <div class="container">
            <p class="font-size-semi-large font-weight-bold">
              {{ $t(`property.property_types`) }}
            </p>
            <span
              class="chip"
              v-for="(propertyType, index) in form2.propertyTypes"
              :key="index">
              {{ propertyType.prop_type_name }}
            </span>
          </div>
        </div>
        <!-- Property Rooms -->
        <div class="section" v-if="form2">
          <div class="container">
            <div class="text">
              <p class="font-size-semi-large font-weight-bold mb-0">
                {{ $t('form.noOfRooms') }}
              </p>
              <div class="number-counter">
                <p>
                  {{ $t('form.bedrooms') }}
                </p>
                <div class="number">
                  <span>{{ form2.bedrooms }}</span>
                </div>
              </div>
              <div class="number-counter">
                <p>
                  {{ $t('property.bathrooms') }}
                </p>
                <div class="number">
                  {{ form2.bathrooms }}
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- Property Area -->
        <div class="section" v-if="form2">
          <div class="container">
            <div class="text">
              <p class="font-size-semi-large font-weight-bold mb-0">
                {{ $t('property.area') }}
              </p>
              <div class="number-counter">
                <p>
                  {{ $t('property.area') }}
                </p>
                <div class="number area">
                  <span>{{ `${form2.area}m` }}<sup>2</sup></span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- Property Bed Types -->
        <div
          class="section"
          v-if="form2 && propertyData && propertyData.bedTypes">
          <div class="container">
            <div class="text">
              <p class="font-size-semi-large font-weight-bold mb-0">
                {{ $t('property.beds_availability') }}
              </p>
              <div
                class="number-counter"
                v-for="(bedType, index) in propertyData.bedTypes"
                :key="index">
                <p>
                  {{ bedType.name }}
                </p>
                <div class="number">
                  <span>{{ form2.bedTypes[index].count }}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- Amenities -->
        <template v-if="form3">
          <div
            class="section"
            v-for="(amenityType, index) in Object.keys(form3)"
            :key="index">
            <div class="container">
              <p class="font-size-semi-large font-weight-bold">
                {{ $t(`form.${amenityType.toLowerCase()}Amenities`) }}
              </p>
              <span
                class="chip"
                v-for="amenity in form3[amenityType]"
                :key="amenity.amenity_id">
                {{ amenity.amenity_name }}
              </span>
            </div>
          </div>
        </template>
      </div>
    </div>
  </b-overlay>
</template>

<script>
export default {
  name: 'AddPropertyPreviewForm',
  props: {
    propertyData: {
      type: Object,
      required: true,
    },
    currentProperty: {
      type: Object | null,
      required: true,
    },
  },
  computed: {
    propertyTitle() {
      const propertyType =
        this.propertyData?.propertyType?.find(
          (prop) =>
            prop.prop_type_id === this.currentProperty?.form2?.propertyType[0]
        )?.prop_type_name || '';

      const propertyCity =
        this.currentProperty?.form1?.city?.location_name || '';
      const propertyDistrict =
        this.currentProperty?.form1?.district?.location_name || '';

      return (
        propertyType &&
        `${propertyType} in ${propertyDistrict}, ${propertyCity}`
      );
    },
    form1() {
      if (this.currentProperty?.form1) {
        const { city, district, neighborhood } = this.currentProperty.form1;

        return {
          city,
          district,
          neighborhood,
        };
      }
      return null;
    },
    form2() {
      if (this.currentProperty?.form2) {
        const { bathrooms, bedTypes, bedrooms, furnishType, propertyType, area } =
          this.currentProperty.form2;

        const furnishTypes = this.propertyData?.furnishType?.filter((item) =>
          furnishType.some((t) => item.id === t)
        );

        const propertyTypes = this.propertyData?.propertyType?.filter((item) =>
          propertyType.some((t) => item.prop_type_id === t)
        );

        return { bathrooms, bedTypes, bedrooms, furnishTypes, propertyTypes, area };
      }
      return null;
    },
    form3() {
      if (this.currentProperty?.form3) {
        const { bathroomAmenities, kitchenAmenities, propertyAmenities } =
          this.currentProperty.form3;

        const Bathroom =
          this.propertyData?.amenities?.Bathroom?.filter((item) =>
            bathroomAmenities?.some((amenity) => item.amenity_id === amenity)
          ) || [];

        const Kitchen =
          this.propertyData?.amenities?.Kitchen?.filter((item) =>
            kitchenAmenities?.some((amenity) => item.amenity_id === amenity)
          ) || [];

        const Property =
          this.propertyData?.amenities?.Property?.filter((item) =>
            propertyAmenities?.some((amenity) => item.amenity_id === amenity)
          ) || [];

        return {
          Bathroom,
          Kitchen,
          Property,
        };
      }
      return null;
    },
    form4() {
      if (this.currentProperty?.form4) {
        const {
          daily_price,
          monthly_price,
          propertyDescription,
          propertyName,
        } = this.currentProperty.form4;
        return {
          daily_price,
          monthly_price,
          propertyDescription,
          propertyName,
        };
      }
      return null;
    },
    form5() {
      return this.$store.state.PropertyModule.propertyImages || null;
    },
  },
};
</script>

<style lang="scss" scoped="true">
.preview-form {
  .section {
    background-color: white;
    padding: 18px 0;
    border-bottom: 1px solid #f2f4f5;
    margin-top: 18px;

    &:last-child {
      border: none;
      padding-bottom: 0;
    }

    &.address {
      .container {
        display: flex;
        align-items: flex-start;

        .svg-icon {
          margin: 2px 16px 0 16px;
        }
      }
    }

    .images {
      width: 100%;
      height: 100%;
      display: flex;
      overflow-y: hidden;
      overflow-x: scroll;
      padding: 8px 0;

      .image {
        margin-right: 12px;

        &:last-child {
          margin: 0;
        }

        img {
          border-radius: 12px;
          object-fit: cover;
        }
      }
    }

    .number-counter {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 16px 0;

      &:last-child {
        border: none;
      }

      p {
        margin: 0;
        font-weight: 600;
        font-size: 16px;
      }

      .number {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
        border: 1px solid #e3e5e5;
        border-radius: 50%;
        &.area {
          width: fit-content !important;
          height: 60px !important;
        }
      }
    }

    .chip {
      display: inline-block;
      padding: 8px 12px;
      background-color: #e6f2ff;
      color: #0078ff;
      font-weight: bold;
      border-radius: 32px;
      margin: 0px 12px 12px 0;

      &:last-child {
        margin: 0;
      }
    }
  }
}

@media screen and (max-width: 560px) {
  .mobile-scroll {
    justify-content: flex-start;
    overflow-x: scroll;
  }
}
</style>
